.page-header {
  //background: conic-gradient(from 30deg at 16.03% 39.06%,#b80027 60deg,#003d85 140deg,#b80027 350deg);
  background: #6a1c52 url("../img/bg_conical_gradient.jpg") no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 658px;
  color: $second-color;
}

.page-header__wrapper {
  width: 96.7%;
  max-width: $desktop-width;
  padding-top: 30px;
  padding-bottom: 51px;
  margin: 0 auto;
}

.page-header__first-line {
  display: flex;
  justify-content: space-between;

  & span,
  & a {
    font-family: 'Bebas Neue', cursive;
    font-size: 26px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    text-decoration: none;

    color: $second-color;
  }

  & a {
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: $second-color-active;
    }
  }
}

.page-header__second-line {
  display: flex;
  justify-content: space-between;
}

.page-header__img {
  margin-top: -5px;
  margin-left: -125px;
}

.page-header__presentation {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 34.22%;
  padding-top: 116px;

  & p {
    margin-bottom: 14px;
    font-family: 'Bebas Neue Cyrillic', cursive;
    font-weight: 200;
    font-size: 49px;
    line-height: 49px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
}

.page-header__tittle {
  position: relative;

  & h1 {
    font-family: 'Bebas Neue Cyrillic', cursive;
    font-size: 109px;
    line-height: 109px;
    text-align: right;
    letter-spacing: 0.03em;
    text-shadow:
       -0   -2px 0   $second-color,
        0   -2px 0   $second-color,
        -0    2px 0   $second-color,
        0    2px 0   $second-color,
        -2px -0   0  $second-color,
        2px -0   0   $second-color,
        -2px  0   0   $second-color,
        2px  0   0   $second-color,
        -1px -2px 0  $second-color,
        1px -2px 0   $second-color,
        -1px  2px 0   $second-color,
        1px  2px 0   $second-color,
        -2px -1px 0  $second-color,
        2px -1px 0   $second-color,
        -2px  1px 0   $second-color,
        2px  1px 0   $second-color,
        -2px -2px 0  $second-color,
        2px -2px 0   $second-color,
        -2px  2px 0   $second-color,
        2px  2px 0   $second-color,
        -2px -2px 0   $second-color,
        2px -2px 0   $second-color,
        -2px  2px 0   $second-color,
        2px  2px 0   $second-color;
  }

  ul {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    bottom: 33px;
    right: 265px;
    font-family: 'Roboto Condensed', sans-serif ;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
  }
}

.page-header__social {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  min-height: 79px;

  & ul {
    margin-top: -50px;
    text-align: right;
    list-style: none;
  }

  & li {
    display: block;
    margin-bottom: 9px;
  }

  & a {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    color: $second-color;

    &:hover,
    &:focus {
      color: $second-color-active;
    }
  }
}

@media (max-width: $tablet-width) {
  .page-header__wrapper {
    width: 88.28%;
    padding-top: 39px;
    padding-bottom: 70px;
  }

  .page-header__first-line {
    margin-bottom: 24px;
  }

  .page-header__first-line a,
  .page-header__first-line span {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }

  .page-header__social {
    display: none;
  }

  .page-header__presentation {
    box-sizing: border-box;
    width: 52.5%;
    padding-top: 52px;
    padding-right: 30px;

    & p {
      margin-bottom: 7px;
      font-size: 34px;
      line-height: 34px;
      letter-spacing: 0.03em;
    }
  }

  .page-header__img {
    width: 47.78%; //324px;
    height: 50.78vw;
    overflow:hidden;
    position:relative;

    & img,
    & picture {
        position:absolute;
        width: 45.7vw;
        height: 100%;
        top: 0;
        right: 0;
      }
    }



  .page-header__tittle h1 {
    font-size: 86px;
    line-height: 86px;
    text-align: right;
    letter-spacing: 0.03em;
  }

  .page-header__tittle ul {
    bottom: 20px;
    right: 212px;
  }

}
