html {
  height: 100%; }

body {
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-family: "Roboto Condensed", sans-serif;
  color: #102654;
  font-style: normal;
  scroll-behavior: smooth; }

img {
  max-width: 100%;
  height: auto; }

picture {
  line-height: 0; }

.visually-hidden:not(:focus):not(:active),
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  z-index: 1; }

.page.no-scroll {
  height: 100%;
  width: 100%;
  position: fixed;
  overflow: hidden; }

.page__wrapper {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.overlay--show {
  display: block; }

.page-header {
  background: #6a1c52 url("../img/bg_conical_gradient.jpg") no-repeat;
  background-size: cover;
  background-position: center center;
