    text-shadow: 0 -2px 0 white, 0 -2px 0 white, 0 2px 0 white, 0 2px 0 white, -2px 0 0 white, 2px 0 0 white, -2px 0 0 white, 2px 0 0 white, -1px -2px 0 white, 1px -2px 0 white, -1px 2px 0 white, 1px 2px 0 white, -2px -1px 0 white, 2px -1px 0 white, -2px 1px 0 white, 2px 1px 0 white, -2px -2px 0 white, 2px -2px 0 white, -2px 2px 0 white, 2px 2px 0 white, -2px -2px 0 white, 2px -2px 0 white, -2px 2px 0 white, 2px 2px 0 white; }
  .page-header__tittle ul {
